<template>
    <div id="nav-manage">
        <header>
            <img src="../assets/img/arrow-left.png" alt="" @click="goToHome">
            <span>
                频道管理
            </span>
        </header>
        <h4>点击添加以下频道</h4>
        <ul class="container">
            <li v-for="(item,index) in editList" :key="index" @click="add(item.id)">{{item.title}}</li>
        </ul>
        <h4>点击删除以下频道</h4>
        <ul class="container">
            <li v-for="(item,index) in currentList" :key="index" @click="remove(item.id)">{{item.title}}</li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'NavManage',
    data(){
        return{
            currentList: [],
            editList: []
        }
    },
    methods: {
        goToHome(){
            this.$router.push({ name: 'Select' })
        },
        add(id){
            this.editList.forEach((item, index)=>{
                if(item.id === id){
                    this.editList.splice(index, 1)
                    this.currentList.push(item)
                }
            })
        },
        remove(id){
            this.currentList.forEach((item, index)=>{
                if(item.id === id){
                    this.currentList.splice(index, 1)
                    this.editList.push(item)
                }
            })
        }
    },
    watch: {
        currentList(val){
            this.$store.dispatch('change1Handler', val)
        },
        editList(val){
            this.$store.dispatch('change2Handler', val)
        }
    },
    created(){
        this.currentList = this.$store.state.navList
        this.editList = this.$store.state.editList
    }
}
</script>

<style lang='less' scoped>
  #nav-manage{
      height: 100%;
      width: 100%;
      background: rgb(241, 241, 241);
        header{
            position: relative;
            height: 2.4em;
            line-height: 2.4em;
            background: #d43d3d;
            padding: 5px 0;
            img{
                position: absolute;
                top: 15%;
                left: 0;
                height: 65%;
            }
            span{
                font-weight: 600;
                color: #fff;
            }
        }
        h4{
            display: block;
            padding: 0 10px;
            line-height: 2em;
            background-color: #f5f5f5;
            font-size: 12px;
            margin-top: 0;
            color: #666;
            text-align: start;
        }
        ul{
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            padding: 0;
            margin: 0;
            min-height: 100px;
            li{
                height: 32px;
                font-size: 16px;
                display: block;
                margin: 0 8px;
                border: 1px solid #ccc;
                padding: 0 10px;
                margin-bottom: 5px;
                text-align: center;
                line-height: 2em;
                background-color: #f0f0f0;
            }
        }
  }
</style>
